@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
body {
  margin: 0;
  font-family:  -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

html, body { 
  height: 100%;
 }


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p{
  padding:40px;
  letter-spacing: -2px;
  font-size: 60px !important;
}

nav {
  background-color: white !important;
}

.nav-item {
  margin-left: 25px;
}

a.nav-link {
  font-size: 25px;
}

@media only screen and (max-width: 480px) {

  p{
    padding:40px;
    letter-spacing: -2px;
    font-size: 40px !important;
  }

}
.wrapper {
  min-height: 100%;
  /* padding-bottom: 100px; */
  font-family: "Merriweather";
  min-height: 100%;
  position: relative;
  padding-bottom: 70px!important;/*Footer height*/
}

.header{
    margin: 20px 0px 20px 0px;
    font-size: 275%;
    font-weight: bold;
    color: white;
    background: rgb(193,53,7);
    background: linear-gradient(90deg, rgba(219, 78, 31, 0.984) 0%, rgba(200,37,71,1) 35%, rgb(175, 7, 44) 100%);
    width: 500px;
    border-radius: 100px;
    text-align:center;
    font-family: "Raleway";
    letter-spacing:2px;
}

.bio-box {
    height: 900px;
    background-color:#1c2d50;
    color:white;
    list-style-type: none;
    border-radius: 4px;
}

div.container-fluid {
    font-family:'Merriweather';
}  

#bio-text {
    font-size: 30px;
    color: white;
}

.bio-list-item{
    word-spacing: 5px;
    margin: 0px 0px 25px 5px;
}

p#resume-link {
    padding-top: 0px !important;
    color: white;
    font-size: 35px !important;
}

img.contact-me-logo {
    height:100px;
    width: 100px;
    background-color:white;
    border-radius: 15px;
}

.contact-container {
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
    width: 33%;

}

.empty-box {
    background-image: url(/react-portfolio/static/media/backsplash2.dd7602d1.jpg);
    background-color: #cccccc;
    object-fit: contain;
    border-radius: 3px;
}

.row.empty-box {
    height: 220px;
}

.row {
    margin-bottom: 30px
}

.tech-box {
    border-radius: 4px;
    background-color:#1c2d50;
    color:rgb(255, 255, 255);
    list-style-type: none;
    margin-top: 30px;
    padding:20px;
    margin-bottom: 50px;
}

#tech-header {
    width: 500px;
    height: 100px;
}

#contact-header {
    width: 500px;
    height: 50px;
}

#picture-box {
    height: 100%;
}

img.pic {
    width: 90%;
    height: 275px;
    object-fit: cover;
    padding:10px;
    border-radius: 200px;
}

.picture-column {
    text-align:center;
}

#picture-grid {
    background-color: rgb(231, 238, 240);
    margin-left: 50px;
}

ul {
    list-style-type: none;
}

ul#logos {
    margin:0;
    -webkit-column-count: 4; 
    column-count: 4;
}

li.logo {
    padding: 10px;
    font-size: 35px;
    text-align: center;
}

img.logo {
    border-radius:5px;
}

@media screen and (max-width: 1024px) {

    img.pic {
        width: 100%;
        height: 300px;
        margin-top: 0px;
    }

    ul#logos {
        margin:0;
        -webkit-column-count: 2; 
        column-count: 2;
    }

    li.logo {
        padding: 10px;
        font-size: 20px;
        text-align: center;
    }

    .header{
        width: 300px;
        font-size:175%
    }

    #tech-header {
        width: 510px;
        height: 80px;
    }

    #bio-text {
        font-size: 30px;
        color: white;
    }

    .row.empty-box {
        height: 100px;
    }

    .contact-container {
        width: 50%;
        margin-top: 10px;
    }
  }

@media screen and (max-width: 480px) {

    img.pic {
    width: 100%;
    height: 300px;
    margin-top: 0px;
    }

    #bio-text {
        font-size: 20px;
        color: white;
    }

    .header{
        width: 300px;
    }

    #tech-header {
        width: 300px;
        height: 100px;
    }

    ul#logos {
        margin:0;
        -webkit-column-count: 1; 
        column-count: 1;
    }

    li.logo {
        padding: 10px;
        font-size: 20px;
        text-align: center;
    }

  }





div.react-3d-carousel {
  margin-top: 75px;
}

.slider-single-content {
  background-color: white;
}

.slider-single-content:hover {
  background-color: #e2e5eb;
}

img {
  height: 500px;
  width: 700px;
}

p.project-description {
  font-family: "Raleway";
  letter-spacing: 2px;
  font-size: 20px !important;
  word-spacing: 5px;
  text-align:center;
  text-decoration: none;
}

a {
  text-decoration: none !important;
  color:#1c2d50 !important;
}

@media only screen and (max-width: 600px) {
  img {
    height: 200px;
    width: 300px;
  }
}
footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  min-height: 100px;
  height:100px;
  text-align: center;
  padding: 0px;
  box-sizing: border-box;
  background-color: #1c2d50;
}

p#footer-text {
  color: white;
  font-size: 30px !important; 
}



  


.main {
  font-family: 'Raleway';
}


 div.main {
   height: 500px;
 }


